import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { serverUrl } from '../const';
import axios from 'axios';


export default function RegisterPage() {
    const navigate = useNavigate();
    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [wechat, setWechat] = useState("");
    const [phone, setPhone] = useState("");

    function onFormSubmit(e){
        e.preventDefault();
        const volunteerInfo = {
            name,
            email,
            wechat,
            phone
        }
        // console.log(volunteerInfo)
        axios.post(serverUrl, { volunteerInfo })
        .then(res => {
        console.log(res);
        console.log(res.data)
        })
        navigate("/choosetime")
    }

  return (
    <div className='row justify-content-center p-5'>
        <div className='col-12 col-md-8'>
            <h1 className='fw-bold my-4'></h1>
        <div className='col-12 col-md-8'>
            ~
            </div>
            <div  className='my-2'>
             RCSSArice
            </div>
            <div  className='my-2 color-light-blue'>
            suvsuv
            </div>
            <form className='mt-3 row' onSubmit={(e)=>onFormSubmit(e)}>
                <div className="my-2 col-12 col-md-6">
                    <div className='fw-bold mb-2'></div>
                    <input type="text" className="form-control" value={name} onChange={(e) => setName(e.target.value)} placeholder=""/>
                </div>
                <div className="my-2 col-12 col-md-6">
                </div>
                <div className="my-2 col-12 col-md-6">
                    <div className='mb-2'><b></b> (@rice.edu)</div>
                    <input type="email" id="email" pattern=".+@rice.edu" value={email} onChange={(e) => setEmail(e.target.value)} className="form-control" placeholder="@rice.edu"/>
                </div>
                <div className="my-2 col-12 col-md-6">
                    <div className='fw-bold mb-2'></div>
                    <input type="text" className="form-control" placeholder="" value={wechat} onChange={e => setWechat(e.target.value)}/>
                </div>
                <div className="my-2 col-12 col-md-6">
                    <div className='mb-2'><b></b> (: 123-456-7890) </div> 
                    <input type="tel" id="phone" className="form-control" name="phone"
                        pattern="[0-9]{3}(-)?[0-9]{3}(-)?[0-9]{4}"
                        value={phone}
                        onChange={e => setPhone(e.target.value)}
                        required/>
                </div>
                {/* <div className="my-2 col-12 col-md-6">
                    <div className='fw-bold mb-2'></div>
                    <input type="text" className="form-control" placeholder=""/>
                </div> */}

                <div className="form-check mt-4">
                    <input className="form-check-input" type="radio" name="flexRadioDefault" id="flexRadioDefault1" required/>
                    <label className="form-check-label" for="flexRadioDefault1">
                        
                    </label>
                </div>
                <div className='d-flex justify-content-end'>
                    <button type='submit' className='btn btn-info homepage-btn my-3'></button>
                </div>
            </form>
        </div>
    </div>
  )
}
